.profesores{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-evenly;
	align-items: center;


	.container-prof{
		width: 30%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

	}
	img{
		border-radius: 50%;
		box-shadow: -13px 14px 8px -7px rgba(0, 0, 0, 0.34);
			-webkit-box-shadow: -13px 14px 8px -7px rgba(0, 0, 0, 0.34);
			-moz-box-shadow: -13px 14px 8px -7px rgba(0, 0, 0, 0.34);
	}
}